import { memo } from "react";
import { useTranslation } from "react-i18next";
import { ShoppingBag, CalendarCheck, MessageCircle, Search, ExternalLink } from "lucide-react";
import { Button } from "@/components/ui/button";
import mapIcon from "@/assets/map-icon.png";
import { useActiveHomepageTemplates } from "@/hooks/useHomepageTemplates";
import { useNavigationSettings } from "@/hooks/useNavigationSettings";

interface MobileHeroSectionProps {
  stats: { order_count: number; reserve_count: number } | null;
  onAskXiaoxiong: () => void;
}

const MobileHeroSection = memo(({ stats, onAskXiaoxiong }: MobileHeroSectionProps) => {
  const { t, i18n } = useTranslation();
  const { data: templates } = useActiveHomepageTemplates();
  const { buttons } = useNavigationSettings();
  const currentLang = i18n.language;

  // Find demo button from navigation settings
  const demoButton = buttons?.find(
    (btn) => btn.label_zh === "演示" || btn.link?.includes("v.xiaoxiong.pt")
  );

  // Check if demo button should be shown
  const showDemoButton = demoButton && 
    demoButton.enabled !== false && 
    demoButton.visible !== false && 
    demoButton.show_on_mobile !== false;

  // Get localized demo button label
  const getDemoLabel = () => {
    if (!demoButton) return t("home.hero.demo", "前往演示");
    if (currentLang === "zh") return demoButton.label_zh;
    if (currentLang === "pt") return demoButton.label_pt;
    return demoButton.label_en;
  };

  // Get localized text from active template
  const getLocalizedText = (field: string) => {
    if (!templates || templates.length === 0) return "";
    const template = templates[0];
    const langSuffix = currentLang === "zh" ? "_zh" : currentLang === "pt" ? "_pt" : "_en";
    return (template as any)[`${field}${langSuffix}`] || "";
  };

  const title = getLocalizedText("title") || t("home.hero.title");
  const description = getLocalizedText("description") || t("home.hero.description");
  const chatButtonText = getLocalizedText("subtitle_button_text") || t("home.hero.subtitle");

  return (
    <section className="min-h-[70vh] flex flex-col bg-secondary/20">
      {/* Header Area - Brand + Demo Button */}
      <div className="pt-20 px-4">
        {/* Top Row: Brand Name + Demo Button */}
        <div className="flex items-center justify-between mb-3">
          {/* Brand Name with Icon */}
          <div className="flex items-center gap-2">
            <span className="text-xl font-bold text-foreground">
              {t("home.hero.brandName")}
            </span>
            <img 
              src={mapIcon} 
              alt="map icon" 
              width={28}
              height={28}
              loading="eager"
              decoding="async"
              className="w-7 h-7 object-contain flex-shrink-0" 
            />
          </div>

          {/* Demo Button - Synced with backend navigation settings */}
          {showDemoButton && demoButton && (
            <Button
              variant="outline"
              size="sm"
              className="gap-1.5 text-xs"
              onClick={() => window.open(demoButton.link, "_blank")}
            >
              <ExternalLink className="w-3.5 h-3.5" />
              {getDemoLabel()}
            </Button>
          )}
        </div>

        {/* Stats Row: Delivery + Booking */}
        {stats && (
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-1.5">
              <ShoppingBag className="w-4 h-4 text-primary" />
              <span className="text-sm text-muted-foreground">{t("home.stats.delivery")}</span>
              <span className="font-bold text-foreground text-sm">{stats.order_count}</span>
            </div>
            <div className="flex items-center gap-1.5">
              <CalendarCheck className="w-4 h-4 text-accent" />
              <span className="text-sm text-muted-foreground">{t("home.stats.reservation")}</span>
              <span className="font-bold text-foreground text-sm">{stats.reserve_count}</span>
            </div>
          </div>
        )}
      </div>

      {/* Content Area - Title, Description, Chat Button */}
      <div className="flex-1 flex flex-col items-center justify-center px-4 py-8">
        <div className="max-w-md mx-auto text-center space-y-4">
          {/* Title */}
          <h1 className="text-2xl sm:text-3xl font-bold text-foreground leading-tight">
            {title}
          </h1>
          
          {/* Description */}
          <p className="text-sm sm:text-base text-muted-foreground max-w-sm mx-auto">
            {description}
          </p>
          
          {/* Chat Button - Ask Xiaoxiong */}
          <div className="pt-4">
            <button 
              onClick={onAskXiaoxiong}
              className="group flex items-center gap-3 bg-secondary/50 backdrop-blur-sm text-foreground px-5 py-3.5 rounded-full shadow-sm hover:shadow-md hover:bg-secondary/70 transition-all duration-300 cursor-text border border-border/50 mx-auto"
            >
              <MessageCircle className="w-5 h-5 text-primary group-hover:text-primary/80 transition-colors" />
              <span className="text-sm text-muted-foreground group-hover:text-foreground transition-colors">
                {chatButtonText}
              </span>
              <Search className="w-4 h-4 text-muted-foreground/60 ml-2" />
            </button>
          </div>
        </div>
      </div>
    </section>
  );
});

MobileHeroSection.displayName = "MobileHeroSection";

export default MobileHeroSection;
